    background-color: #777;
    -webkit-box-shadow: 0 1px 1px #444;
            box-shadow: 0 1px 1px #444;
    border-radius: 500px;
    margin-top: -15px;
}
.marginfix {
    margin: 0;
}
vue-nav-bar {
    bottom: 0px;
}
@media screen and (min-width: 1000px) {
.wrapper {
        margin: auto;
        width: 980px;
}
}
@media screen and (min-width: 1920px) {
.wrapper {
        margin: auto;
        width: 1280px;
}
}
@media screen and (max-width: 600px) {
body {
        padding: 0;
        margin: 0;
}
.wrapper {
        margin: 0;
        width: 100%;
}
}

